<template>
	<div>
		<ul class="feedback_ul">
			<li class="feedback_con">选择反馈类型</li>
			<li class="feedback_select">
				<select name="" class="select">
					<option value="">入户相关</option>
					<option value="">报考相关</option>
					<option value="">培训相关</option>
					<option value="">平台相关</option>
				</select>
			</li>
		</ul>
		
		<p class="contents">填写反馈内容（0/500）</p>
		<textarea rows="13" cols="20" class="textareas" placeholder="多行输入">
			
		</textarea>
		
		<div class="button_lack" >提交</div>
		<div class="button_whole" @click="show">提交</div>
		<p class="hints">温馨提示：意见反馈限1小时执行1次</p>
		
		<Bottom></Bottom>
	</div>
</template>

<script>
	import Bottom from '../../components/Bottom/Bottom'
	export default{
		components:{
			Bottom
		},
		data(){
			return {
				
			}
		},
		methods:{
			show(){
				this.$popup({
					title: '提交成功',
          			content: '您反馈的事项我们已收到，我们将会在1-2个工作日内转到相关部门处理，感谢您的支持。'
				}).then((res)=>{
					console.log(res)
					if(res=='sure'){
						return;
					}
				})
			}
		}
	}
</script>

<style scoped="scoped" lang="scss">
	@import "../../sass/baseSass.scss";
	
	input[type=text], input[type=password], input[type=submit], select { border: #cccccc 1px solid; height: .8rem; border-radius: .1rem; width: 100%; }
	input[type=text]:focus, input[type=password]:focus, input[type=submit]:focus, select:focus { border-color: #fa8a12; }
	input[type=text], input[type=password] { padding: 0 .1rem; }
	input[type=submit] { background-color: #fa8a12; text-align: center; width: 100%; }
	textarea { height: auto; min-height: 1.2rem; border: #cccccc 1px solid; border-radius: .1rem; padding: .2rem .3rem; width: 100%; }
	input:-moz-placeholder { color: #999999; }
	::-webkit-input-placeholder { color: #999999; }
	input[type=text], input[type=password] { -webkit-appearance: none; }
	
	.feedback_ul{
		padding: 0 0.3rem;display: flex;height: 0.8rem;line-height: 0.8rem;position: relative;margin-top: 0.3rem;
		.feedback_con{font-size: 0.3rem;font-weight: 800;}
		.feedback_select{
			position: absolute;top: -0.3rem;right: 0.3rem;width: 4.5rem;
			.select{padding-left: 0.2rem;height: 0.8rem;}
		}
	}
	.contents{padding: 0 0.3rem;height: 1rem;line-height: 1rem;font-size: 0.3rem;font-weight: 800;}
	.textareas{width: 91%;margin: 0 auto;margin-left: 0.3rem;}
	.button_lack{width: 6.9rem;height: 1rem;background-color: #ccc;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.3rem auto 0.3rem;}
	.button_whole{width: 6.9rem;height: 1rem;background-color: #fa8a12;font-size: 0.34rem;color: #FFFFFF;text-align: center;line-height: 1rem;border-radius:0.5rem ;margin:0.3rem auto 0.3rem;}
	.hints{font-size: 0.3rem;color: #fa8a12;padding-left: 0.3rem;}
</style>


